<template>
  <div>
    <PersonalHeader></PersonalHeader>
    <div class="centerBg">
      <div class="wrapper clearfix">
        <div class="centerLeft">
          <PersonalMenu class="fl" currentMenu="6"></PersonalMenu>
        </div>
        <div class="centerRight">
          <div class="centerTitle">
            <ul>
              <router-link to="/account">
                <li>账户中心</li>
              </router-link>
              <router-link to="/investorManage">
                <li class="active">投资者管理</li>
              </router-link>
              <router-link to="/leaveManage">
                <li>留言管理</li>
              </router-link>
            </ul>
            <span class="brandBtn">我的品牌页 &gt;</span>
          </div>
          <div class="invBox">
            <ul class="invNav clearfix" @click="tabChange">
              <li :class="[read == 0 ? 'active':'']" data-read="0">待跟进</li>
              <li :class="[read == 1 ? 'active':'']" data-read="1">正在跟进</li>
              <li :class="[read == 2 ? 'active':'']" data-read="2">已签约</li>
              <li :class="[read == 3 ? 'active':'']" data-read="3">已放弃</li>
            </ul>
            <div class="invList">
              <dl v-for="(item,index) in data" :key="'man'+item.id">
                <dt class="clearfix">
                  <div class="data fl">{{item.name}}<span>|</span>{{item.sex == 1? '男':'女'}}<span>|</span>{{item.phone}}
                  </div>
                  <div class="status fr">
                    <i>{{item.time}}</i>
                    <span @click="detail" :data-index="index">查看</span>
                  </div>
                </dt>
                <dd>{{item.content}}</dd>
              </dl>
            </div>

            <el-pagination background layout="prev, pager, next" :total="count" class="pages"
                           @current-change="pageChange"></el-pagination>

          </div>
        </div>
        <el-dialog
          :title="title"
          :visible.sync="dialogVisible"
          width="40%"
           class="model1" center>
          <div class="box">
            <span>姓名：{{currentData.consult.name}}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
            <span>性别：{{currentData.consult.sex == 1? '男':'女'}}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
            <span>电话：{{currentData.consult.phone}}</span>
            <div class="consult">
              {{currentData.consult.content}}
            </div>
          </div>
          <div class="box">
            <div>
              <span class="title">投资意向：</span>
              <span class="iconfont icon-xingxing currentColor" @click="intentionChange(1)"></span>
              <span class="iconfont icon-xingxing" :class="[currentData.consult.intention >1 ?'currentColor':'']" @click="intentionChange(2)"></span>
              <span class="iconfont icon-xingxing" :class="[currentData.consult.intention >2 ?'currentColor':'']" @click="intentionChange(3)"></span>
              <span class="iconfont icon-xingxing" :class="[currentData.consult.intention >3 ?'currentColor':'']" @click="intentionChange(4)"></span>
              <span class="iconfont icon-xingxing" :class="[currentData.consult.intention >4 ?'currentColor':'']" @click="intentionChange(5)"></span>
            </div>
            <div>
              <span class="title">选择状态：</span>
              <label for="" :class="[currentData.consult.read == 1 ?'currentColor':'']" @click="statusChange(1)">
                <span class="iconfont icon-dian" ></span>正在跟进&nbsp;&nbsp;
              </label>
              <label for="" :class="[currentData.consult.read == 3 ?'currentColor':'']" @click="statusChange(3)">
                <span class="iconfont icon-dian" ></span>放弃跟进&nbsp;&nbsp;
              </label>
              <label for="" :class="[currentData.consult.read == 2?'currentColor':'']" @click="statusChange(2)">
                <span class="iconfont icon-dian" ></span>已经签约&nbsp;&nbsp;
              </label>
            </div>
            <div>客户跟踪日志：</div>
            <div class="logs" v-if="currentData.con_log.length>0">
              <div class="log" v-for="item in currentData.con_log" :key="'log'+item.id">
                <div>{{item.content}}</div>
                <div class="time">{{item.addtime}}</div>
              </div>
            </div>
            <textarea type="textarea" v-model="content" class="addlog" placeholder="请在此添加日志~"></textarea>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="addConsult" >保 存</el-button>
            </span>
          </div>

        </el-dialog>
      </div>
    </div>
  </div>
</template>

<script>
  import PersonalHeader from '@/components/PersonalHeader'
  import PersonalMenu from '@/components/PersonalMenu'
  import api from '@/api/personal-api'

  export default {
    name: "index.vue",
    data() {
      return {
        dialogVisible: false,
        consultId: '',
        read: 0,
        type: 0,
        page: 0,
        data: '',
        count: 0,
        currentData: {consult:{
            name:'',
            content:'',
            read:'',
            addtime:'',
          },con_log:[]
        },
        detailName: ['待跟进','正在跟进','已签约','放弃跟进','无效'],
        title:'',
        content: [],
        status: 1 // 0 无效，1有效

      }
    },
    created() {
      this.getData();
    },
    methods: {
      getData: function () {
        let {read, page,status} = this;
        if(read == 4){
          status = 0;
        }
        api.getInvertorManage({type: 0,status, read, page}).then(data => {
          this.data = data.data;
          this.count = data.count
        });
      },

      tabChange: function ({target: {dataset: {read}}}) {
        this.read = read;
        this.page=0
        this.getData();
      },
      /**
       * 更改跟进状态
       */
      statusChange: function(read){
        this.currentData.consult.read = read;

      },
      /**
       * 修改意向状态
       */
      intentionChange: function(value){
        this.currentData.consult.intention = value;
      },
      /**
       * 点击分页
       * @param e
       */
      pageChange: function (e) {
        this.page = e - 1;
        window.scrollTo(0, 0);
        this.getData();
      },

      /**
       * 查看详情
       */
      detail: function ({currentTarget: {dataset: {index}}}) {
        api.getConsultDetail({id:this.data[index].id,type:0}).then(data =>{
          this.currentData = data;
          this.title = this.detailName[data.consult.read];
          this.dialogVisible = true;
        })
      },
      /**
       * 添加跟进
       */
      addConsult: function(){
         const { read,intention,id} = this.currentData.consult;
         const {content} = this;
         api.addConsult({id,int:intention,read,content}).then(data=>{
           this.dialogVisible = false;
           this.content = '';
           this.$message.success('保存成功')
         });

      }
    },
    components: {
      PersonalHeader,
      PersonalMenu
    }
  }
</script>

<style lang="less" type="text/less" scoped>
  .currentColor {
    color: #579EFA !important;
  }

  .icon-xingxing {
    color: #D4D5D9
  }

  .centerBg {
    width: 100%;
    height: auto;
    padding: 20px 0 100px;
    background: #F4F5FA;
  }

  .centerLeft {
    float: left;
    width: 230px;
  }

  .centerRight {
    float: right;
    width: 950px;
    min-height: 610px;
    background: #fff;

    .centerTitle {
      padding: 0 33px;
      height: 60px;
      border-bottom: 1px solid #E3E4E5;

      ul {
        li {
          height: 58px;
          line-height: 58px;
          float: left;
          color: #666;
          font-size: 16px;
          border-bottom: 2px solid #fff;
          margin-right: 50px;
        }

        li.active {
          color: #333;
          border-bottom: 2px solid #4F74F4;
        }
      }

      .brandBtn {
        float: right;
        color: #666;
        line-height: 60px;
      }
    }

    .invBox {
      .invNav {
        width: 100%;
        height: 60px;
        line-height: 60px;
        background: #F0F7FF;
        margin-bottom: 20px;

        li {
          float: left;
          padding: 0 40px 0 33px;
          color: #666;
          cursor: pointer;
        }

        li.active {
          color: #5389F7;
        }
      }

      .invList {
        padding: 0 30px;

        dl {
          width: 100%;
          padding: 0 0 18px;
          border-bottom: 1px dashed #E3E4E5;

          dt {
            height: 65px;
            line-height: 65px;

            .data {
              color: #333;

              span {
                margin: 0 15px;
                color: #D7D8D9;
              }
            }

            .status {
              i {
                color: #999;
                font-style: normal;
              }

              span {
                color: #5389F7;
                margin-left: 25px;
                cursor: pointer;
              }
            }
          }

          dd {
            padding: 20px 22px;
            color: #808080;
            background: #F4F5FA;
          }
        }

      }
    }
  }

  .pages {
    text-align: center;
    margin: 30px 0;
  }


  /* 弹窗样式 */
  .model1 {
    .box {
      font-size: 14px;
      color: rgba(51, 51, 51, 1);
      line-height: 24px;
      padding: 25px 45px;
      border-top: 1px solid #E3E4E5;
      span,label{
        cursor: pointer;
      }
      .title{
        cursor: default;
      }
      .consult {
        padding: 20px 23px;
        background-color: #F4F5FA;
        margin-top: 20px;
      }

      .logs {
        padding: 13px;
        border: 1px solid #E3E4E5;

        .time {
          color: #999;
          text-align: right;
        }
      }

    }
  }

  .addlog {
    width: 100%;
    padding: 23px;
    background-color: #F4F5FA;
    min-height: 90px;
    margin-top: 20px;
    resize: none;
    box-sizing: border-box;
  }

  .dialog-footer {
    display: flex;
    justify-content: center;

  }
</style>



